<template>
  <transition name="fade">
    <div v-show="modal" class="font-general-regular fixed inset-0 z-30">
      <!-- Modal body background as backdrop -->
      <div
        v-show="modal"
        @click="showModal()"
        class="bg-filter bg-black bg-opacity-50 fixed inset-0 w-full h-full z-20"
      ></div>
      <!-- Modal content -->
      <main class="flex flex-col items-center justify-center h-full w-full">
        <transition name="fade-up-down">
          <div v-show="modal" class="modal-wrapper flex items-center z-30">
            <div
              class="modal max-w-md mx-5 xl:max-w-xl lg:max-w-xl md:max-w-xl bg-secondary-light dark:bg-primary-dark max-h-screen shadow-lg flex-row rounded-lg relative"
            >
              <div
                class="modal-header flex justify-between gap-10 p-5 border-b border-ternary-light dark:border-ternary-dark"
              >
                <h5 class="text-primary-dark dark:text-primary-light text-xl">
                  ¿Qué proyecto estás buscando?
                </h5>
                <button
                  class="px-4 text-primary-dark dark:text-primary-light"
                  @click="showModal()"
                >
                  <i data-feather="x"></i>
                </button>
              </div>
              <div class="modal-body p-5 w-full h-full">
                <form
                  class="max-w-xl m-4 text-left"
                  @submit.prevent="contactar"
                >
                  <div class="mt-0">
                    <input
                      class="w-full px-5 py-2 border-1 border-gray-200 dark:border-secondary-dark rounded-md text-md bg-secondary-light dark:bg-ternary-dark text-primary-dark dark:text-ternary-light"
                      id="name"
                      type="text"
                      placeholder="Nombre completo (*)"
                      aria-label="Name"
                      v-model="formContacto.name"
                      :class="{ error: formError.name }"
                    />
                  </div>
                  <div class="mt-6">
                    <input
                      class="w-full px-5 py-2 border-1 border-gray-200 dark:border-secondary-dark rounded-md text-md bg-secondary-light dark:bg-ternary-dark text-primary-dark dark:text-ternary-light"
                      id="email"
                      type="text"
                      placeholder="Correo electrónico (*)"
                      aria-label="Email"
                      v-model="formContacto.email"
                      :class="{ error: formError.email }"
                    />
                  </div>
                  <div class="mt-6">
                    <select
                      class="w-full px-5 py-2 border-1 border-gray-200 dark:border-secondary-dark rounded-md text-md bg-secondary-light dark:bg-ternary-dark text-primary-dark dark:text-ternary-light"
                      id="subject"
                      type="text"
                      aria-label="Project Category"
                      v-model="formContacto.subject"
                      :class="{ error: formError.subject }"
                    >
                      <option
                        v-for="category in categories"
                        :key="category.id"
                        :value="category.value"
                      >
                        {{ category.name }}
                      </option>
                    </select>
                  </div>

                  <div class="mt-6">
                    <textarea
                      class="w-full px-5 py-2 border-1 border-gray-200 dark:border-secondary-dark rounded-md text-md bg-secondary-light dark:bg-ternary-dark text-primary-dark dark:text-ternary-light"
                      id="message"
                      cols="14"
                      rows="6"
                      aria-label="Details"
                      placeholder="Descripción del proyecto (*)"
                      v-model="formContacto.message"
                      :class="{ error: formError.message }"
                    ></textarea>
                  </div>

                  <div class="mt-6 pb-4 sm:pb-1">
                    <Button
                      title="Enviar solicitud"
                      class="px-4 sm:px-6 py-2 sm:py-2.5 text-white bg-indigo-500 hover:bg-indigo-600 rounded-md focus:ring-1 focus:ring-indigo-900 duration-500"
                      type="submit"
                      aria-label="Submit Request"
                    />
                  </div>
                </form>
              </div>
              <!-- <div
                class="modal-footer mt-2 sm:mt-0 py-5 px-8 border0-t text-right"
              >
                <Button
                  title="Cerrar"
                  class="px-4 sm:px-6 py-2 bg-gray-600 text-primary-light hover:bg-ternary-dark dark:bg-gray-200 dark:text-secondary-dark dark:hover:bg-primary-light rounded-md focus:ring-1 focus:ring-indigo-900 duration-500"
                  @click="showModal()"
                  aria-label="Close Modal"
                />
              </div> -->
            </div>
          </div>
        </transition>
      </main>
    </div>
  </transition>
</template>

<script>
import { ref, inject } from "vue";
import * as Yup from "yup";
import { ContactarApi } from "../api/contratar";
import feather from "feather-icons";
import Button from "./reusable/Button.vue";

export default {
  props: ["showModal", "modal", "categories"],
  components: { Button },
  data() {
    return {};
  },
  mounted() {
    feather.replace();
  },
  updated() {
    feather.replace();
  },
  methods: {},
  setup() {
    const formContacto = ref({});
    let formError = ref({});
    const toast = inject("WKToast");

    const schemaForm = Yup.object().shape({
      name: Yup.string().required(true),
      email: Yup.string().email(true).required(true),
      subject: Yup.string().required(true),
      message: Yup.string().required(true),
    });

    const contactar = async () => {
      formError.value = {};

      var form = {
        data: formContacto.value,
      };

      try {
        await schemaForm.validate(formContacto.value, { abortEarly: false });
        try {
          const response = await ContactarApi(form);
          console.log(response);
          toast("¡Solicitud enviada!", {
            className: ["my-toast"],
            horizontalPosition: "left",
            verticalPosition: "bottom",
            duration: 3000,
            transition: "transition",
          });

          formContacto.value = {};
        } catch (error) {
          console.log(error);
        }
      } catch (error) {
        error.inner.forEach((err) => {
          formError.value[err.path] = err.message;
        });
      }
    };

    return {
      formContacto,
      formError,
      contactar,
    };
  },
};
</script>

<style scoped>
.modal-body {
  max-height: 500px;
}
.bg-gray-800-opacity {
  background-color: #2d374850;
}
@media screen and (max-width: 768px) {
  .modal-body {
    max-height: 400px;
  }
}
.fade-up-down-enter-active {
  transition: all 0.3s ease;
}
.fade-up-down-leave-active {
  transition: all 0.3s ease;
}
.fade-up-down-enter {
  transform: translateY(10%);
  opacity: 0;
}
.fade-up-down-leave-to {
  transform: translateY(10%);
  opacity: 0;
}

.fade-enter-active {
  -webkit-transition: opacity 2s;
  transition: opacity 0.3s;
}
.fade-leave-active {
  transition: opacity 0.3s;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

input.error {
  background: rgb(238, 169, 169);
  border: 2px solid rgb(241, 125, 125);
  color: white !important;
}
textarea.error {
  background: rgb(238, 169, 169);
  border: 2px solid rgb(241, 125, 125);
  color: white !important;
}

select.error {
  background: rgb(238, 169, 169);
  border: 2px solid rgb(241, 125, 125);
  color: white !important;
}
</style>
